.wrapperList{
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
}

.wrapperItem{
    position: relative;
    width: 380px;
height: 460px; 
// display: flex;
// flex-direction: column;
justify-content: center;
text-align: center;

background: linear-gradient(114.99deg, #471CA9 -0.99%, #5736A3 54.28%, #4B2A99 78.99%);
box-shadow: -2.5777px 6.87386px 20.6216px rgba(0, 0, 0, 0.23);
border-radius: 20px;
// background-image: url('../../../components/img/picture2\ 1.png');
// background-repeat: no-repeat;
// background-size: contain;
// background-position: top;
}
.logo{
    position: absolute;
  top: 20px;
  left: 20px;
}
.wrapperItem::after {
  content: '';
  position: absolute;
  top: 28px;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url('../../../components/img/picture2\ 1.png');
  background-position: top;
  background-size: 308px, contain;
  background-repeat: no-repeat;
  z-index: 99; /* Отрицательное значение z-index */
}
.wrapperItem::before {
  content: '';
  position: absolute;
width: 380px;
height: 8px;
left: 0px;
top: 214px;

background: #EBD8FF;
box-shadow: 0px 3.43693px 3.43693px rgba(0, 0, 0, 0.06), inset 0px -1.71846px 3.43693px #AE7BE3, inset 0px 3.43693px 2.5777px #FBF8FF;
}
.wrapperAvatar{
 position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80px;
  height: 80px;
  left: 150px;
  top: 178px;

  background: #ebd8ff;

  box-shadow: 0px 4.39163px 4.39163px rgba(0, 0, 0, 0.06),
    inset 0px -2.19582px 4.39163px #ae7be3,
    inset 0px 4.39163px 3.29372px #fbf8ff;

  border-radius: 85.9232px;
}
.avatar{
  width: 62px;
  height: 62px;
  background-color: #5736a3;
  border-radius: 85.9232px;
  box-shadow: 0px 4.39163px 4.39163px rgba(0, 0, 0, 0.06),
    0px -2.19582px 4.39163px #ae7be3, 0px 4.39163px 3.29372px #fbf8ff;
}

